













































































































// @ is an alias to /src
import Header from "@/components/Header.vue";
import { Toast } from "vant";
import { Component, Vue, Watch } from "vue-property-decorator";
@Component({
    components: {
        Header
    }
})
export default class DetailUser extends Vue {
    private show: boolean = false;
    private back: boolean = true;
    private showSelectDate?: boolean = false;
    private showGenderSheet?: boolean = false;
    private currentDate?: any = new Date();
    private minDate?: any = new Date(1970, 0, 1);
    private maxDate?: any = new Date();
    private selectedImageItem = 0;
    private userDetails?: any = {
      avatar: require('@/assets/user/icone_top_edit.png'),
      name: 'test001',
      actualName: 'iAmTest',
      email: 'test001@gmail.com',
      phoneNumber: '1111111111',
      gender: this.$t('message.male'),
      dataOfBirth: '2000.01.01',
    };
    private gender?: any[] = [
      { name: this.$t('message.male') },
      { name: this.$t('message.female') },
    ];
    private images: any[] = [
        { name: require('@/assets/user/person1.png') },
        { name: require('@/assets/user/person2.png') },
        { name: require('@/assets/user/person3.png') },
        { name: require('@/assets/user/person4.png') },
        { name: require('@/assets/user/person5.png') },
        { name: require('@/assets/user/person6.png') },
        { name: require('@/assets/user/person7.png') },
        { name: require('@/assets/user/person8.png') }
    ];
    private selectAvatar(index: any, icon: any) {
      this.selectedImageItem = index;
      this.userDetails.avatar = icon;
    }
    private uploadImage(file: any) {
      this.userDetails.avatar = file.content;
    }
    private onSelectGender(item: any) {
      this.userDetails.gender = item.name;
      this.showGenderSheet = false;
    }
    private selectDate(val: any) {
      let month = (val.getMonth() + 1) < 10 ? `0${(val.getMonth() + 1)}` : (val.getMonth() + 1);
      let date = val.getDate() < 10 ? `0${val.getDate()}` : val.getDate();

      let newDOB = `${val.getFullYear()}.${month}.${date}`;
      this.userDetails.dataOfBirth = newDOB;
      this.showSelectDate = false;
    }
    onCancel() {
      this.userDetails.avatar = require('@/assets/user/icone_top_edit.png');
    }
}
